import React from 'react';
import { Header, WhyChooseUs, ContactUs, AboutUs } from '../components/Content';
import Menu from '../components/Menu'
import MediaQuery from 'react-responsive';
import { Helmet } from "react-helmet";


export default class Home extends React.Component {
	componentDidMount() {
		window.scrollTo(0, 0)
	}

	render() {
		return (
			<div className='container'>
				<Helmet>
					<meta charSet="utf-8" />
					<title> |  |  - CAR ATM</title>
					<meta name="description" content=" 30  -        " />
					<link rel="canonical" href="https://caratmthailand.com" />
					<meta name="robots" content="index, follow" />
					<meta property="og:type" content="article" />
					<meta property="og:title" content=" |  |  - CAR ATM" />
					<meta property="og:description" content=" 30  -        " />
					<meta property="og:url" content="https://caratmthailand.com" />
					<meta property="og:site_name" content="caratmthailand.com" />
				</Helmet>
				<Header
					style={{ marginBottom: 20 }}
					slot={
						<div>
							<MediaQuery maxWidth={768}>
								<h1 className='largest__text blue'>
									<br />
								</h1>
							</MediaQuery>
							<MediaQuery minWidth={769}>
								<h1 className='largest__text blue'>
									
								</h1>
							</MediaQuery>
						</div>
					}
				/>

				<Menu />
				<ContactUs />
				<WhyChooseUs />
				<AboutUs
					slot={
						<MediaQuery maxWidth={1024}>
							<ContactUs />
						</MediaQuery>
					}
				/>
			</div>
		);
	}
}
