import React from 'react';
import { Header, WhyChooseUs, ContactUs, AboutUs, Tab, Address, SocialMedia, Map, LazyLoadBox } from '../components/Content';
import GetStarted from '../components/GetStarted';
import ExpansionPanel from '../components/ExpansionPanel';
import GetStartedImage1 from '../assets/img/content/get_start_6.png';
import GetStartedImage2 from '../assets/img/content/get_start_2.png';
import GetStartedImage3 from '../assets/img/content/get_start_3.png';
import GetStartedImage4 from '../assets/img/content/get_start_4.png';
import HeaderImage from '../assets/img/header/refinance.png';
import WarningImage from '../assets/img/warning_icon.svg';
import MediaQuery from 'react-responsive';
import { Helmet } from "react-helmet";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import LogoImage from '../assets/img/logo.png';
import { withRouter } from 'react-router-dom'
import axios from 'axios';

class Refinance extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			currentTab: 0,
			carrRefinanceFaqData: [],
			guarantorFaqData: [],
			isLoading: true
		};
	}

	scrollTo = (ready) => {	
		if(ready){
			let el = document.getElementById(this.props.history.location.hash.substr(1));
			if (el !== null){
				setTimeout(function(){
					el.scrollIntoView(true);
				}, 10)
			}
		}
	}

	pushRoute = async (url) => {
		await this.props.history.push("/carrefinance/faq#" + url)
	}

	setCurrentTab = async (data) => {
		await this.setState({ currentTab: data })

		switch (this.state.currentTab) {
			case 0:
				await this.props.history.push("/carrefinance");
				break;
			case 1:
				await this.props.history.push("/carrefinance/faq");
				break;
			default:
				await this.props.history.push("/carrefinance");
				break;
		}

		let tab = document.getElementById('tab');

		if (tab.getBoundingClientRect().top > 100){
			window.scrollTo(0, 0);
		}else{
			document.getElementById('tab__ref').scrollIntoView(true);
		}
	}

	async componentWillMount() {
		var carrRefinanceFaqLoading = true;
		var guarantorFaqLoading = true;
		await axios.get(process.env.REACT_APP_API_URL + '/api/carrefinanceFAQ', {
			headers: {
				'Content-Type': 'application/json',
			}
		}).then((response) => {
			this.setState({carrRefinanceFaqData : response.data});
			carrRefinanceFaqLoading = false;
			this.setState({isLoading: carrRefinanceFaqLoading || guarantorFaqLoading})
			this.scrollTo(!this.state.isLoading)
		})

		await axios.get(process.env.REACT_APP_API_URL + '/api/guarantorFaq', {
			headers: {
				'Content-Type': 'application/json',
			}
		}).then((response) => {
			this.setState({guarantorFaqData : response.data});
			guarantorFaqLoading = false;
			this.setState({isLoading: carrRefinanceFaqLoading || guarantorFaqLoading})
			this.scrollTo(!this.state.isLoading)
		})
	}

	componentDidMount() {
		window.scrollTo(0, 0)
		this.setState({ currentTab: this.props.currentTab ? this.props.currentTab : 0 })
	}

	render() {
		var carrRefinanceFaq = <LazyLoadBox/>;
		var guarantorFaq = <LazyLoadBox/>;
		if (!this.state.isLoading) {
			carrRefinanceFaq = [];
			guarantorFaq = [];

			for(let i = 0; i < this.state.carrRefinanceFaqData.length; i++){
				carrRefinanceFaq.push(
					<div key={i}>
						<div id={this.state.carrRefinanceFaqData[i].url} className='scroll__ref'></div>
						<ExpansionPanel
							url={this.state.carrRefinanceFaqData[i].url}
							callback={this.pushRoute}
							expand={this.props.history.location.hash.substr(1) === this.state.carrRefinanceFaqData[i].url}
							summary={<h3 className='text__20 text__thai' style={{textAlign: 'left'}}>{this.state.carrRefinanceFaqData[i].header}</h3>}
							detail={
								<p className='dark__blue text__faq__body' dangerouslySetInnerHTML={{__html: this.state.carrRefinanceFaqData[i].body}}/>
							}
						/>
					</div>
				)
			}

			for(let i = 0; i < this.state.guarantorFaqData.length; i++){
				guarantorFaq.push(
					<div key={i}>
						<div id={this.state.guarantorFaqData[i].url} className='scroll__ref'></div>
						<ExpansionPanel
							url={this.state.guarantorFaqData[i].url}
							callback={this.pushRoute}
							expand={this.props.history.location.hash.substr(1) === this.state.guarantorFaqData[i].url}
							summary={<h3 className='text__20 text__thai' style={{textAlign: 'left'}}>{this.state.guarantorFaqData[i].header}</h3>}
							detail={
								<p className='dark__blue text__faq__body' dangerouslySetInnerHTML={{__html: this.state.guarantorFaqData[i].body}}/>
							}
						/>
					</div>
				)
			}
		}

		const summaryText01 = <p>?</p>;

		const summaryText02 = <p>?</p>;

		const detailText01 =
			<p className='dark__blue'>
				 <br />
				<br />
				 <br />
				() <br />
				
			</p>;

		const detailText02 =
			<p className='dark__blue'>
				  <font style={{ fontWeight: 'bold' }}></font>  <br />
				  2004/2547<br />
				 <font style={{ fontWeight: 'bold' }}></font> 2 
			</p>;

		let screenData = null;
		switch (this.state.currentTab) {
			case 0:
				screenData =
					<div>
						<ContactUs
							headerText={<p className='dark__blue'> CAR ATM</p>}
							warning={
								<span className='warning__text__large'>
									<LazyLoadImage src={WarningImage} alt='warning' style={{ height: '50%', marginRight: 16 }} />
									<p>
										     2004 (2547)<br/>
										 <br/>
										 
									</p>
								</span>
							}
						/>

						<MediaQuery maxWidth={768}>
							<ExpansionPanel
								summary={summaryText01}
								detail={detailText01}
							/>
							<ExpansionPanel
								summary={summaryText02}
								detail={detailText02}
							/>
						</MediaQuery>

						<MediaQuery minWidth={769}>
							<div className='duo__text__container'>
								<div className='left__text__container' style={{ alignItems: 'flex-end' }}>
									<div>
										<div className='large__text dark__blue'>
											{summaryText01}
										</div>
										<div className='medium__text dark__blue detail__text' style={{ marginTop: 30 }}>
											{detailText01}
										</div>
									</div>
								</div>
								<div className='right__text__container'>
									<div className='large__text dark__blue'>
										{summaryText02}
									</div>
									<div className='medium__text dark__blue detail__text' style={{ marginTop: 30 }}>
										{detailText02}
									</div>
								</div>
							</div>
						</MediaQuery>

						<GetStarted
							getStartedData={
								[{
									image: GetStartedImage1,
									imageAlt: '',
									headerText:
										<p className='dark__blue'>1. <font className='blue'>?</font></p>,
									headerTextMobile:
										<p className='dark__blue'> ?</p>,
									bodyText:
										<p className='dark__blue' style={{ textAlign: 'left' }}>
											  <font style={{ fontWeight: 'bold' }}></font> <br></br>
											 <font style={{ fontWeight: 'bold' }}> 2004/2547</font><br></br>
											 <font style={{ fontWeight: 'bold' }}></font> 2 
										</p>,
									bodyTextMobile:
										<p className='dark__blue' style={{ textAlign: 'left', paddingLeft: 30, paddingRight: 30 }}>
											  <font style={{ fontWeight: 'bold' }}></font> <br></br>
											 <font style={{ fontWeight: 'bold' }}> 2004/2547</font><br></br>
											 <font style={{ fontWeight: 'bold' }}></font> 2 
										</p>,
								}, {
									image: GetStartedImage2,
									imageAlt: ' ',
									headerText:
										<p className='dark__blue'>2.<font className='blue'></font></p>,
									headerTextMobile:
										<p className='dark__blue'></p>,
									bodyText:
										<p className='dark__blue'>
											<br></br>
											   <br></br>
											<br></br>
											<font className='text__bold'></font>, <font className='text__bold'></font>, <font className='text__bold'></font>
										</p>,
									bodyTextMobile:
										<p className='dark__blue'>
											<br></br>
											<br></br>
											 <br></br>
											<br></br>
											1. <font className='text__bold'></font><br></br>
											2. <font className='text__bold'></font><br></br>
											3. <font className='text__bold'></font>
										</p>
								}, {
									image: GetStartedImage3,
									imageAlt: '',
									headerText:
										<p className='dark__blue'>3. <font className='blue'></font></p>,
									headerTextMobile:
										<p className='dark__blue'></p>,
									bodyText:
										<p className='dark__blue'>
											<br></br>
											 <br></br>
											<font className='text__bold'>,</font> <font className='text__bold'>,</font> <font className='text__bold'>,</font><br></br>
											<font className='text__bold'></font> <br></br>
											 
										</p>,
									bodyTextMobile:
										<p className='dark__blue'>
											<br></br>
											 <br></br>
											<font className='text__bold'>,</font><br></br>
											<font className='text__bold'>,</font><br></br>
											<font className='text__bold'>,</font><br></br>
											<font className='text__bold'></font> <br></br>
											 
										</p>
								}, {
									image: GetStartedImage4,
									imageAlt: '',
									headerText:
										<p className='dark__blue'>4.<font className='blue'></font> <font className='blue'></font></p>,
									headerTextMobile:
										<p className='dark__blue'>  </p>,
									bodyText:
										<p className='dark__blue'>
											 <font className='text__bold'></font><br></br>
											 <br></br>
											( 2-3 )
										</p>,
									bodyTextMobile:
										<p className='dark__blue'>
											<br></br>
											<font className='text__bold'></font><br></br>
											<br></br>
											<br></br>
											( 2-3 )
										</p>
								}]
							}

						/>
						<WhyChooseUs />

						<AboutUs
							slot={
								<MediaQuery maxWidth={1024}>
									<ContactUs
										headerText={<p className='dark__blue'> CAR ATM</p>}
										warning={
											<span className='warning__text__large'>
												<LazyLoadImage src={WarningImage} alt='warning' style={{ height: '50%', marginRight: 16 }} />
												<p style={{ display: 'flex', flexDirection: 'column' }}>
													     2004 (2547)<br/>
													 <br/>
													 
												</p>
											</span>
										}
									/>
								</MediaQuery>
							}
						/>
					</div>
				break;

			case 1:
				screenData =
					<div>
						<div className='content' style={{ backgroundColor: '#F2F2F2', maxWidth: 'none' }}>
							<div style={{ display: 'flex', flexDirection: 'column', maxWidth: 728, width: '100%' }}>
								<MediaQuery minWidth={728}><div style={{ height: 35 }}></div></MediaQuery>
								{carrRefinanceFaq}

								<MediaQuery minWidth={728}><div style={{ height: 35 }}></div></MediaQuery>
								<div style={{ alignSelf: 'flex-start', marginLeft: 30, marginBottom: 16 }}>
									<p className="larger__text header__text dark__blue" style={{ marginBottom: 0 }}>
										
									</p>
									<p className="medium__text dark__blue" style={{ textAlign: 'start' }}>
										()
									</p>
								</div>

								{guarantorFaq}
							
							</div>
						</div>
						<div id='contact' className='scroll__ref'></div>
						<ContactUs
							headerText={
								<p className='larger__text dark__blue' style={{ flexDirection: 'row', marginTop: -10 }}>
									<font style={{ color: '#EB5757' }}></font>
								</p>}
							promotionText={false}
							subHeaderText={
								<p className='dark__blue' style={{ marginTop: 5 }}> !</p>
							}
							warning={<div></div>}
						/>
						<div className='content'>
							<LazyLoadImage src={LogoImage} alt='Logo' className='about__logo__image' />
							<Address />
							<SocialMedia />
							<Map />
						</div>
					</div>
				break;
			default:
				break;
		}

		return (
			<div className='container'>
				<Helmet>
					<meta charSet="utf-8" />
					<title> |  30  - CAR ATM</title>
					<meta name="description" content="       " />
					<link rel="canonical" href="https://caratmthailand.com/carrefinance" />
					<meta name="robots" content="index, follow" />
					<meta property="og:type" content="article" />
					<meta property="og:title" content=" |  30  - CAR ATM" />
					<meta property="og:description" content="       " />
					<meta property="og:url" content="https://caratmthailand.com/carrefinance" />
					<meta property="og:site_name" content="caratmthailand.com" />
				</Helmet>

				<Header
					slot={
						<div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
							<h1 className='largest__text blue'>
								
							</h1>

							<LazyLoadImage src={HeaderImage} alt='Header' className='header__image' />
						</div>
					}
				/>
				<Tab tabs={['', '']} activeTab={this.state.currentTab} callback={this.setCurrentTab} id='tab'/>
				<div>
					<div id='tab__ref' className='scroll__ref'></div>
				</div>
				{screenData}
			</div>
		);
	}
}

export default withRouter(Refinance)